<template>
	<view class="messageUp">
		<view @click="uploadImages" class="messageUpC messageUpB disRowcCenter">
			<image :src="add" class="registeredLogoAdd"></image>
		</view>
		<view class="messageUpC" v-for="(item,index) in detailList" :key="index">
			<image :src="registeredLogo" class="registeredLogo"></image>
			<image :src="item.imgUrl" @tap="previewsImg(index)" class="registeredLogoBac"></image>
		</view>


	</view>
</template>

<script>
	//多文件上传 https://blog.csdn.net/haoyue1014/article/details/89790247
	import {
		getPrivateAuth,
		save,
		getValidUrl,
		getPrivateUploadAuth,
		filebindCardh
	} from "@/api/index.js";
	export default {
		props: ['optionsDatas'],
		data() {
			return {
				detailList: [{
					"imgUrl": 'https://ss0.baidu.com/-Po3dSag_xI4khGko9WTAnF6hhy/baike/pic/item/728da9773912b31b8bd836558618367adab4e112.jpg'
				}, {
					"imgUrl": 'https://ss1.baidu.com/-4o3dSag_xI4khGko9WTAnF6hhy/baike/pic/item/2cf5e0fe9925bc3180db888b5cdf8db1cb13701b.jpg'
				}, {
					"imgUrl": 'https://ss1.baidu.com/-4o3dSag_xI4khGko9WTAnF6hhy/baike/pic/item/2cf5e0fe9925bc3180db888b5cdf8db1cb13701b.jpg'
				}, {
					"imgUrl": 'https://ss1.baidu.com/-4o3dSag_xI4khGko9WTAnF6hhy/baike/pic/item/2cf5e0fe9925bc3180db888b5cdf8db1cb13701b.jpg'
				}],
				registeredLogo: require('../../static/images/lxg/attachmentDel.png'),
				add: require('../../static/images/lxg/add.png'),
				fileName: "",
				token: "",
			}
		},

		components: {},

		computed: {},
		created() {

		},
		methods: {
			async getfilebindCardh(optionsData, fileIdArray) {
				console.log('==222optionsData=====>', optionsData);
				let params = {
					"processNodeTblId": optionsData.processNodeTblId,
					"tblId": optionsData.tblId,
					"fileIdArray": fileIdArray
				}
				await filebindCardh(params).then(response => {

					console.log('==返回的数据=====>', response);
				});

			},
			async postsave(itemd) {
				let self = this;
				let item = JSON.parse(itemd)

				let items = {
					"bucket": item.bucket,
					"format": item.format,
					"mimeType": item.mimeType,
					"saveName": item.saveName,
					"upName": item.upName,
					"validUrL": item.hash
				}
				await save(items).then(res => {
					let fileIdArray = []
					fileIdArray.push(res.data.id) 
					self.getfilebindCardh(self.optionsDatas, fileIdArray)
					console.log("========>", res)
				})
			},
			async getToken() {
				let self = this; 
				const timestamp = +new Date() + '';
				const randomNum = parseInt((1 + Math.random()) * 65536) + '';
				let fileName = (+(randomNum + timestamp)).toString(32); 
				await getPrivateUploadAuth({}).then((res) => {
					console.log("token", res)
					if (res.code === 200) {

						let token = res.data;
						self.fileName = fileName
						self.token = token  

					}
				});
			},
			async uploadImages() {
				await this.getToken()
				let self = this

				let fileNames = this.fileName
				let tokens = this.token
				uni.chooseImage({
					count: 6, //默认9

					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有

					sourceType: ['album'], //从相册选择

					success: function(res) {
						console.log("选择的图片", JSON.stringify(res.tempFilePaths));
						let tempFilePaths = res.tempFilePaths

						for (var i = 0; i < tempFilePaths.length; i++) {
							 
							uni.uploadFile({
								url: 'https://up-z2.qiniup.com',
								filePath: tempFilePaths[i],
								name: 'file',
								formData: {
									'key': fileNames,
									'token': tokens
								},
								success: (uploadFileRes) => {
									self.postsave(uploadFileRes.data)
									console.log("js上传", uploadFileRes); 
								},
							});

						}
					}

				});

			},
			previewsImg() {
				let imgUrl = this.detailList
				this.$upLoad.previewsImg(imgUrl)
			},
			previewImage(index) {
				console.log("=======>", this.detailList[index].lunboImg)
				var i = this.detailList[index].lunboImg; //获取当前页面的轮播图数据
				//uniapp预览轮播图
				uni.previewImage({
					current: index, //预览图片的下标
					urls: i //预览图片的地址，必须要数组形式，如果不是数组形式就转换成数组形式就可以
				})
			}
		},

		onReady() {}
	};
</script>
<style scoped>
	.messageUp {
		padding-left: 40upx;
		/* background-color: #0077AA; */
		display: flex;
		flex-direction: row;
		align-items: center;
		flex-wrap: wrap;
	}

	.messageUpC {
		width: 152upx;
		height: 152upx;

		margin-bottom: 20upx;
		margin-right: 20upx;
		position: relative;
	}

	.messageUpB {
		border: 1px dashed #999999;
	}

	.registeredLogo {
		width: 36upx;
		height: 36upx;
		overflow: hidden;
		border-radius: 18upx;
		position: absolute;
		right: -4px;
		top: -4px;
		z-index: 88;
	}

	.registeredLogoBac {
		width: 100%;
		height: 100%;
	}

	.registeredLogoAdd {
		width: 40upx;
		height: 40upx;
	}
</style>
